import React, {useState, useCallback, useMemo} from 'react'
import {Link, useHistory, useRouteMatch} from 'react-router-dom'
import {connect} from 'react-redux'
import {signup, checkStatus, updatePassword, login} from '../../store/action/userAction'
import Loading from '../../utils/Loading'
import {userFormHook} from '../../utils/checkInput'
import FormGroup from '../content/FormGroup'


function Signup(props) {
    const [loading, setLoading] = useState()
    const history = useHistory()
    const path = useRouteMatch().path
    const setCon = () => {
        let initialState
        let handleSubmit
       
        if(useRouteMatch('/user/newSignup')) {
           initialState = {
                err: [],
                title: 'Sign Up',
                data: {
                    name: '',
                    email: '',
                    password: '',
                    passwordConfirm: ''
                },
                type: {
                    name: 'text',
                    email: 'text',
                    password: 'password',
                    passwordConfirm: 'password'
                },
                validator: {
                    name: 'empty',
                    email: 'email',
                    password: 'password',
                    passwordConfirm: 'passwordConfirm'
                }
            }
            handleSubmit = (e) => {
                e.preventDefault()
                props.dispatch(signup(state.data, setLoading))
            }
        } else if (useRouteMatch('/user/changePassword')) {
            initialState =  {
                err: [],
                title: 'Change Password',
                data: {
                    passwordOld: '',
                    password: '',
                    passwordConfirm: ''
                },
                type: {
                    passwordOld: 'password',
                    password: 'password',
                    passwordConfirm: 'password'
                },
                validator: {
                    passwordOld: 'empty',
                    password: 'password',
                    passwordConfirm: 'passwordConfirm'
                }
            }
                handleSubmit = (e) => {
                    e.preventDefault()
                    props.dispatch(updatePassword(state.data, history, setLoading))
                }
              
        } else if (useRouteMatch('/user/login')) {
            initialState = {
            
                err: [],
                title: 'Log In',
                data: {
                    email: '',
                    password: ''
                },
                type: {
                    email: 'text',
                    password: 'password'
                },
                validator: {
                    email: 'email',
                    password: 'empty'
                }
            }
            handleSubmit = (e) => {
                e.preventDefault()
                props.dispatch(login(state.data, history))
            }

        }
        return [initialState, handleSubmit]
    }
    const [initialState, handleSubmit] = useMemo(setCon, [path])
    const [state, dispatch] = userFormHook(initialState)
    console.log(state)
    const findErr = (err, name) => {
        if(err.length > 0) {
            const foundErr = err.find((el) => {
                if(name === el.type) {
                    return el
                }
            })
            if(foundErr) return foundErr.value
        }
    }
  
    const stateReady = (state) => {
       const empty = []
       Object.keys(state.data).map(el => {if(state.data[el].length === 0) {empty.push(el)}})
       if(empty.length === 0 && state.err.length === 0 ) {
           return true
       }
       return false
    }
    const handler = (act, value) => {
        dispatch({type:'CHECK', act, value})
    }
        if(loading==='loading') {
            return (    
            <Loading />
            )
        } else if(loading==='success') {
            
            return (
                <div>
                    <div>Đăng ký hoàn thành. Kiểm tra email để kích hoạt tài khoản</div>
                    <Link to ='/' onClick={() => props.dispatch(checkStatus())}>Quay lại trang chủ</Link>
                 </div>
            )
        }
        else {
            return (
                <form className='form' onSubmit = {(e) => handleSubmit(e)}>
                 {props.error && <div className='form__box'><div className='error-notification error-notification--form'>{props.error}</div></div>}
                        {Object.keys(state.data).map((el) => {
                            return <FormGroup name={el} handler = {handler} err={findErr(state.err, el)}
                            type={state.type[el]}
                            />
                        })}
                        <div className='form__box'>
                            <a className={stateReady(state) ? 'btn btn-action' : 'btn btn-action--disable'}
                            onClick={(e) => {
                                dispatch({type: 'RE_CHECK_EMPTY'})
                                if(state.err.length === 0) {
                                   setLoading('loading')
                                   handleSubmit(e)
                                }
                           
                            }}
                            onKeyPress={(e) => {
                                dispatch({type: 'RE_CHECK_EMPTY'})
                                if(state.err.length === 0) {
                                   setLoading('loading')
                                   handleSubmit(e)
                                }
                           
                            }}
                            
                            >{state.title}
                        </a>
                        
                        
                    </div>
                </form>
            )
        
    }
}
const mapStateToProps = (state) => {
    return {
        error: state.user.error
    }
}
export default connect(mapStateToProps)(Signup)
