import React from 'react'
import keyTranslate from '../../utils/translateKey'
function LookupDetail({data, filter}) {
    if(data) {
        const Objkey = Object.keys(data)
        let key = Objkey.filter(el => !el.includes('id'))
        //sort obj key in methods in order
        if(filter === 'methods') {
            const name = []
            const duration = []
            const rest = []
            const type = []
            key.map((el) => {
                if(el==='name') {
                    name.push(el)
                } else if(el.includes('type')){
                    type.push(el)
                }
                else if(el.includes('Duration')) {
                    duration.push(el)
                } else {
                    rest.push(el)
                }
            })
            key = [...name, ...type, ...duration, ...rest]
            console.log(key)
        }
        return (
            <>
                {
                    key.map(el =>  {
                        if(filter === 'methods') {
                            return (
                                <div className='rightpanel__item'>
                                    <span className='rightpanel__key'>{keyTranslate(filter, el)}: </span>
                                        {
                                            el==='step' ? <> {data[el].map(step => {
                                                return (
                                                    <div>
                                                        <span className='rightpanel__subkey'>{step.stepName}</span>
                                                        {
                                                        step.media.map(media => {
                                                            return (
                                                                <ul className='rightpanel__sublist'>
                                                                    <li>Tên môi trường: {media.displayName}</li>
                                                                    <li>Số lượng môi trường: {media.mediaQuantity}-{media.mediaUnit}</li>
                                                                    <li>Nhiệt độ ủ: {media.temp}</li>
                                                                    <li>Thời gian ủ: {media.time}</li>
                                                                </ul>
                                                            )
                                                        })}
                                                    </div>
                                                )
                                            })}</>
                                            : keyTranslate(filter, data[el].toString(), el)
                                        
                                        }
                                    </div>
                            )
                        } else if(filter === 'media') {
                            return (
                                <div className='rightpanel__item'>
                                    <span className='rightpanel__key'>{keyTranslate(filter, el)}: </span>
                                    {keyTranslate(filter, data[el].toString(), el)}
                                </div>
                            )
                        }
                    })
                        
                }
            </>
        )
    } else {
        return <div>Welcome</div>
    }
    
}

export default LookupDetail