import React, { useEffect, useState } from 'react'
import {connect} from 'react-redux'
import fetchData from '../../utils/fetchData'
import {checkStatus} from '../../store/action/userAction'
import Icon from '../../img/spinner6.svg'
import {useParams, Link} from 'react-router-dom'

function SignupConfirm(props) {
    const {token} = useParams()
    const initialComp = (
        <div className='leftpanel__item leftpanel__item--spinner'>
            <Icon className='icon--spinner'/>
            <div className='header-tertinary'>Please wait...</div>
        </div>
    )
    const [resultComp, setResultComp] = useState(initialComp)
    useEffect(() => {
        fetchData('get', `/user/activate/${token}`).then((res) => {
            if(res.status === 'success') {
                setResultComp(<div className='error-notification'>Account activate success. 
                     <Link to='/' onClick={() => props.dispatch(checkStatus())} >Quay lai Trang chu</Link>
                </div>
                   
                )
            } else {
                setResultComp(<div className='error-notification'>Account activate fail.</div>)
            }
        })
    }, [])
    return resultComp
}

export default connect()(SignupConfirm)